body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-X: hidden;
}
a,h1,h2,h3,h4 {
  padding: 0;
  margin: 0;
}
button,
button:active,
button:focus,
input,
input:active,
input:focus
{
  outline: none;
}
.container{
  max-width: 1400px;
  width: 100%;
  margin: 0 auto;
}
.Menu{
  width: 100%;
  position: absolute;
}
.menu-container{
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding-top: 50px;
}
.menu-navigation a{
  margin: 22px;
  transition: .3s;
  color: white;
}
.menu-navigation a:hover{
  color: black;
}
.menu-btn{
  border-radius: 2px;
  border: 1px solid white;
  padding: 10px 10px 12px 10px;
  color: white;
  transition: .3s;
  cursor: pointer;
}
.menu-btn:hover{
  background-color: white;
  color: black;
}
.Header {
  height: 100vh;
  background-image: url(./components/Images/3.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.header-container{
  display: flex;
  width: 100%;

}
.header-text_content{
  width: 50%;

}
.header-text_content h1{
  color: white;
  font-size: 55px;
  padding-bottom: 5px;
}
.header-text_content h2{
  color: white;
  font-size: 18px;
}
.header-text_content button{
  font-size: 18px;
  margin-right: 30px;
  padding: 15px;
  border-radius: 5px;
  color: #061e37;
  border: none;
  transition: .3s;
  margin-top: 35px;
}
.header-text_content button:hover{
  color: #ffffff;
  background-color: #061e37;
}
.header-video{
  display: flex;
  justify-content: flex-end;
  width: 50%;
}






.Info_section{
}
.menu-img{
  width: 15%;
  display: flex;
}
.menu-img img{
  width: 100%;
}
.main_text{
  padding: 100px 0 90px 0;
  text-align: center;
}
.main_text h1{
  margin-bottom: 40px;
  color: #061e37;
}
.main_text h2{
  color: #c5cfd4;
}
.main_text h3{
  color: #c5cfd4;
  width: 50%;
  margin: 0 auto;
}
.features_container{
  width: 100%;
  display: flex;
  align-items: baseline;
}
.features_content{
  width: 33.33333%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.features_text{
  text-align: center;
  width: 90%;
}
.features_text h2{
  padding-bottom: 20px;
}
.features_img{
  padding-bottom: 50px;
}
.features_btn{
  padding-top: 75px;
  display: flex;
  justify-content: center;
  padding-bottom: 95px;

}
.features_btn button{
  padding: 25px;
  text-transform: uppercase;
  background-color: #1de9b6;
  border: none;
  border-radius: 5px;
  color: white;
  box-shadow: 0 4px 10px gray;
  transition: .5s;
  cursor: pointer;
}
.features_btn button:hover{
  background-color: white;
  color:#1de9b6;
  box-shadow:0 1px 7px #1de9b6;

}
.features_cart{
  display: flex;
  width: 100%;
  overflow: hidden;

}
.features_cart-container{
  width: 25%;
  display: flex;
  padding: 50px 100px 50px 50px;
  background-color: #80d8ff;
  justify-content: center;
  align-items: center;
}
.features_cart-container:nth-child(2){
  background-color: #82b1ff;
}
.features_cart-container:nth-child(3){
  background-color: #b388ff;
}
.features_cart-container:nth-child(4){
  background-color: #ea80fc;
}

.features_cart-img{
  padding-right: 10px;
}
.features_cart-text{
  color: white;
}
.services-text{
  display: flex;
  flex-direction: column;
  text-align: center;
}
.services-text h1{
  padding:100px 0  25px 0;
}
.services-text h2{
  padding-bottom: 50px;
}



.toggle-cart-content{
  padding-bottom: 100px;
}


.toggle-cart{
  padding:20px 20px 0 20px;
  display: flex;
  border-radius: 5px;
  margin-bottom: 20px;
  width: 50%;
  flex-direction: column;
  box-shadow: inset 0 1px 10px 0 gray;
  margin-right:auto;margin-left:auto;
  transition: .5s;
}
.toggle-cart:hover{
  box-shadow: inset 0 1px 20px 0 gray;
}
.toggle-cart-container{
  display: flex;
}
.toggle-cart-visable{
  display: flex;
  margin-bottom:20px ;
  align-items: center;

}
.toggle_cart-icon{
  padding-right: 30px;
}
.toggle_drop-content{
  display: none;

}
.toggle_drop-content.close{
  display: flex;
  padding-bottom: 20px;
}

.toggle_drop-content-img{
  padding:0 30px 20px 0;
  width: 70%;
  margin: 0 auto;
}
.toggle_drop-content-img img{
  width: 100%;
}






.reviews{
  display: flex;
  width: 100%;
  background-image: url(./components/Images/18.png);
  background-size: cover;
  background-repeat: no-repeat;
  align-items: center;
}
.reviews_container{
  padding: 50px;
}
.reviews_container-text{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 50%;
  margin: auto;
  color: white;

}
.reviews_container-text h3{
  padding: 30px 0 30px 0;
}

.slider{
  display: flex;
  justify-content: center;
}


.slider-component{
  display: flex !important;
  width: 50% !important;
  margin: auto;
  align-items: center;
  background-color: white;
  border-radius: 5px;
  -webkit-box-shadow: -1px 10px 77px 3px rgba(0,0,0,0.58);
  -moz-box-shadow: -1px 10px 77px 3px rgba(0,0,0,0.58);
  box-shadow: -1px 10px 77px 3px rgba(0,0,0,0.58);
}
.slider-img{
  padding: 50px;
  display: flex;
  width: 60%;

}
.slider-img img{
  width: 100%;
}
.slider-text{
  font-size: 10px;
  padding: 30px;
}
.slider-text h2{
  padding: 20px;
}

.Contact{
  background-color: #fafafa;
}
.contact-container-text{
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 40px 0 40px 0 ;
  width: 50%;
}
.contact-container-text h1{
  padding: 0 0 25px 0 ;
}

.contact-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
  margin: 0 auto;
}
.contact_1-row,.contact_2-row{
  padding: 20px;
  display: flex;
  justify-content: space-between;
}
.contact_3-row{
  padding: 20px;
  width: 80%;
  height: 100px;
  margin: 0 auto;
  display: flex;
}
.contact_3-row input{
  width: 100%;
  margin: 0 auto;
}
input{
  padding: 20px;
  margin: 0 40px 0 40px;
  border-radius: 10px;
  border: 1px solid black;

}
.contact-send-btn{
  padding: 25px;
  text-transform: uppercase;
  background-color: #1de9b6;
  border: none;
  border-radius: 5px;
  color: white;
  box-shadow: 0 4px 10px gray;
  transition: .5s;
  margin-bottom: 100px;
  cursor: pointer;
}
.contact-send-btn:hover{
  background-color: white;
  color:#1de9b6;
  box-shadow:0 1px 7px #1de9b6;
}
.footer{
  background-color:#061e37;
  padding-bottom: 50px;
}
.footer-container{
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding-top: 50px;
}
.footer-navigation a{
  margin: 22px;
  transition: .3s;
  color: #a0a0a0;
  cursor: pointer;
}
.footer-navigation a:hover{
  color: white;
}
.footer-img{
  cursor: pointer;
}



@media (max-width: 1400px) {
 .features_cart{
   flex-direction: column;
 }
  .features_cart-container{
    width: 100%;
    padding: 20px;
  }
}
@media (max-width: 1000px) {
  .toggle_drop-content-img{
    padding-right: 10px;
    width: 200%;
  }
  .slider-img{
    padding: 10px;
  }
}
@media (max-width: 800px) {
  .menu-container{
    padding-top: 10px;
  }
  .header-text_content h1{
    font-size: 35px;
  }
  .header-text_content h2{
    font-size: 20px;
  }
  .menu-btn{
    padding: 5px 5px 6px 5px;
    font-size: 10px;

  }
  .menu-img{
    display: flex;
    width: 20%;
  }

  .header-text_content button{
    font-size: 15px;

  }
  .menu-navigation a{
    margin: 10px;
    font-size: 10px;

  }
  .features_text{
    font-size: 14px;
  }
  .toggle_drop-content-text{
    font-size: 10px;
  }
  .slider-text{
    font-size: 6px;
    padding: 15px;
  }
  .slider-img {
    padding: 5px;
  }
  .reviews_container-text{
    font-size: 10px;
  }
  .footer-navigation a{
    margin: 5px;
  }
  .contact-container-text{
    font-size: 15px;
  }
  .contact-container-text h3{
    font-size: 10px;
  }

}
@media (max-width: 500px) {
  .menu-navigation a{
    font-size: 10px;
  }
  .menu-btn{
    font-size: 10px;
  }
  .header-text_content h1{
    font-size: 25px;
  }
  .header-text_content h2{
    font-size: 12px;
  }
  .header-text_content button{
    font-size: 10px;
  }
  .features_text {
    font-size: 11px;
  }
  .toggle_drop-content-text {
    font-size: 7px;
  }
  .reviews_container-text h3{
    font-size: 15px;
  }
  .slider-text {
    font-size: 5px;
  }
  .footer-navigation a{
    font-size: 12px;
  }
  .contact-container-text{
    font-size: 10px;
  }
}


